<template>
  <div>
    <toolbar>
      <button @click="add">新增学员</button>
    </toolbar>
    <table>
      <tr class="th">
        <th>姓名</th>
        <th>年龄</th>
        <th>班级</th>
        <th>课时</th>
        <th class="operation">操作</th>
      </tr>
      <tr v-for="s in students">
        <td>{{s.name}}</td>
        <td>{{s.age}}</td>
        <td>{{s.clazz.name}}</td>
        <td>
          <button @click="scoreMinus(s)">-</button>
          <input type="text" class="score"
                 v-model="s.score"
                 @change="scoreChange(s)">
          <button @click="scoreAdd(s)">+</button>
        </td>
        <td>
          <button @click="update(s)">更新</button>
          <button @click="remove(s)">删除</button>
        </td>
      </tr>
    </table>
    <router-view></router-view>
  </div>
</template>

<script>
  import Axios from 'axios';

  export default {
    data() {
      return {
        students: []
      }
    },
    created() {
      this.getUserList()
    },
    beforeRouteUpdate(to,from,next){
	this.getUserList();
	next();
    },
    methods: {
      getUserList() {
        Axios
          .get('/api/userList')
          .then(res => {
            this.students = res.data.students;
          })
          .catch(err => console.log(err))
      },
      scoreChange({_id, score}) {
        Axios
          .post('/api/setScore', {_id, score})
          .then(res => {
            if (!res.data.success) {
              //更新失败，刷新数据
              this.getUserList();
            }
          })
          .catch(err => console.log(err))
      },
      scoreAdd(s){
        this.$set(s,'score',s.score+1);
        this.scoreChange(s);
      },
      scoreMinus(s){
        this.$set(s,'score',s.score-1);
        this.scoreChange(s);
      },
      add() {
        this.$router.push({name: 'studentAdd'})
      },
      update(s){
        this.$router.push({name: 'studentUpdate',params:s})
      },
      remove(s){
        if(confirm('确定删除'))
          Axios
            .delete('/api/user/'+s._id)
            .then(res => this.getUserList())
            .catch(err => console.log(err))
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .operation {
    width: 100px;
  }

  .score {
    width: 35px;
    text-align: center;
  }
</style>



